import React from 'react';
import { Button, Typography, List } from 'antd';
import interactor from '../../../interactor/app.interactor';
const { Text } = Typography;
class Sections extends React.Component {
    renderSections = (data) => {
        const elements = data.map((item, idx) => {
            return (
                <div className='section-div' key={idx}>
                    <p className='section-title accent'>{item.name}</p>
                    <Text>{item.description}</Text>
                    {
                        item.list ?
                            (
                                <List
                                    header={item.list_header}
                                    dataSource={item.list}
                                    renderItem={text => (
                                        <List.Item>	â€¢ {text}</List.Item>
                                    )}
                                />
                            )
                            :
                            null
                    }

                </div>
            )
        });

        return elements;
    }

    renderProjects = (data) => {
        const elements = data.map((item, idx) => {
            return (
                <div key={idx} className='card'>
                    <p className='project-title sub-accent'>{item.title}</p>
                    <Text type='secondary'>{item.date}</Text>
                    <br />
                    <Text>{item.description}</Text>
                    <br />
                    <Button key={idx} style={{ whiteSpace: 'pre-wrap'}} onClick={e => window.open(item.url, '_blank')} type="link">{item.url}</Button>
                    <br />
                </div>
            );
        })

        return elements;
    }

    render() {
        const { profile } = this.props;
        const { sections, projects, resource } = profile;
        return (
            <div>
                <p className='section-title accent'>resources</p>
                {interactor.renderTagButton(resource)}
                <br />
                <br />
                {this.renderSections(sections)}
                <br />
                <div>
                    {
                        projects && projects.length > 0 ?
                            <div>
                                <p className='section-title accent'>Projects</p>
                                <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                                    {this.renderProjects(projects)}
                                </div>
                            </div>
                            :
                            null
                    }
                </div>
                <br />
            </div>
        );
    }
}

export default Sections;
