import React from 'react';
import { Button, Tag } from 'antd';
const PROFILE_URL = 'https://dl.dropboxusercontent.com/s/os3637ycu86wgcw/profile.json';
class HomeInteractor {
    static getProfile = async () => {
        let profile = null;
        try {
            let response = await fetch(PROFILE_URL);
            profile = response.json();
        } catch (error) {
            console.error('Unable to get profile');
        }

        return profile;
    }


    static renderTags = (data, color) => {
        const elements = data.map((item, idx) => {
            return <Tag key={idx} color={color}>{item.title}</Tag>
        })

        return elements;
    }

    static renderTagButton = (data) => {
        const elements = data.map((item, idx) => {
            return <Button key={idx} onClick={e => window.open(item.url, '_blank')} type="link"><Tag color='#108ee9'>{item.title}</Tag></Button>
        })

        return elements;
    }

    static renderIconButton = (links) => {
        const elements = links.map((item, idx) => {
            return <Button key={idx} className='profile-button' onClick={e => window.open(item.url, '_blank')} type="link" icon={item.icon}></Button>
        })

        return elements;
    }

    static renderCustomCard = (links) => {

    }
}

export default HomeInteractor;