import React from 'react';
import { Row, Col, Icon } from 'antd';
import interactor from '../interactor/app.interactor';
import Profile from './component/profile/profile.component';
import Sections from './component/sections/sections.component';
import './home.component.css';
class Home extends React.Component {
  constructor() {
    super();
    this.state = { profile: null, error: null };
  }

  componentDidMount() {
    this.setup();
  }


  setup = async () => {
    try {
      const profile = await interactor.getProfile();
      if (profile) {
        this.setState({ profile: profile });
      } else {
        this.setState({ error: 'Profile data not found' });
      }
    } catch (error) {
      this.setState({ error: 'Profile data not found' });
    }
  }

  renderProfilePage = (profile) => {
    return (
      <div>
        <Row>
          <Col xs={24} sm={24} md={0} lg={0} xl={0}>
            <Profile profile={profile} />
            <br />
          </Col>
        </Row>
        <Row gutter={16}>
          <Col xs={0} sm={0} md={6} lg={6} xl={6}>
            <Profile profile={profile} />
          </Col>
          <Col xs={24} sm={24} md={18} lg={18} xl={18}>
            <Sections profile={profile} />
          </Col>
        </Row>
        <Row>
          <div style={{ height: '10vh' }} />
        </Row>
      </div>
    )
  }

  render() {
    const { profile, error } = this.state;
    return (
      <div className='home-page'>
        {
          profile ? this.renderProfilePage(profile) : (error ? <h1>{error}</h1> : <Icon style={{ fontSize: '32px'}} type="loading" />)
        }
      </div>
    )
  }
}

export default Home;
