import React from 'react';
import { Typography } from 'antd';
import interactor from '../../../interactor/app.interactor';
const { Text } = Typography;
class Profile extends React.Component {
    render() {
        const { profile }  = this.props;
        const { name, picture, position, employer, summary, languages } = profile;
        return (
            <div>
                <img className='profile-picture' src={picture} alt="Profile Picture" width="100" height="100" />
                <p className='profile-title accent'>{name}</p>
                <p className='project-title sub-accent'>{summary}</p>
                <br />
                <div style={{ display: 'flex', flexDirection: 'column' }}>
                    <Text className='profile-employer' type="secondary">{position}</Text>
                    <Text className='profile-employer' type="secondary">{employer}</Text>                    
                </div>
                <br />
                {interactor.renderTags(languages, 'blue')}
            </div>
        );
    }
}

export default Profile;
